<template>
  <b-container fluid class="mt--6">
    <card>
      <b-row align-v="center" slot="header">
        <b-col cols="8">
          <h3 class="mb-0">{{ $t('cruds.tables.edit.title1') }}</h3>
        </b-col>
      </b-row>

      <validation-observer ref="formValidator">
        <form-loader height_custom="410px" v-if="loading" />

        <b-form v-else role="form">
          <div class="pl-lg-4">
            <b-row>
              <b-col lg="6">
                <h6 class="heading-small text-muted mb-4">{{ $t('cruds.tables.edit.title1') }}</h6>

                <b-row>
                  <b-col lg="12">
                    <base-input :name="$t('cruds.tables.edit.area_id')" :label="$t('cruds.tables.edit.area_id')"
                      :rules="{ required: true }">
                      <el-select :value="entry.area_id" :placeholder="$t('cruds.tables.edit.area_id')"
                        @input="updateAreaId">
                        <el-option v-for="option in lists.areas" :key="option.label" :label="option.label"
                          :value="option.value">
                        </el-option>
                      </el-select>
                    </base-input>
                  </b-col>
                </b-row>

                <b-row>
                  <b-col lg="12">
                    <base-input class="mb-3" :placeholder="$t('cruds.tables.edit.link')" disabled
                      :name="$t('cruds.tables.edit.link')" :label="$t('cruds.tables.edit.link')"
                      :value="entry.table_link" />
                  </b-col>
                </b-row>

              </b-col>

              <b-col lg="6" v-if="entry.id">
                <h6 class="heading-small text-muted mb-4">{{ $t('cruds.tables.edit.title2') }} <strong>{{ $t('cruds.tables.title') }} N째 {{ entry.id }}</strong></h6>

                <img alt="Image placeholder" class="card-image"
                  :src="entry.table_link" />
              </b-col>

              <div class="text-center">
                <b-button type="submit" @click.prevent="indexSubmit" variant="primary" class="mt-4">{{ $t('general.save')
                }}</b-button>
                <b-button type="submit" @click.prevent="createSubmit" variant="default" class="mt-4">{{
                  $t('general.save_and_continue') }}</b-button>
              </div>
            </b-row>

          </div>
        </b-form>
      </validation-observer>
    </card>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters("TableSingle", ["entry", "loading", "lists"]),
  },
  watch: {
    "this.$route.params.id": {
      immediate: true,
      handler() {
        this.resetState();
        this.fetchEditData(this.$route.params.id);
      },
    },
  },
  beforeDestroy() {
    this.resetState();
  },
  methods: {
    ...mapActions("TableSingle", [
      "updateData",
      "fetchEditData",
      "resetState",
      "setAreaId"
    ]),
    updateAreaId(value) {
      this.setAreaId(value);
    },
    createSubmit() {
      this.$refs.formValidator.validate().then((valid) => {
        if (valid) {
          this.updateData()
            .then(() => {
              this.$eventHub.$emit("update-success");
              window.location.reload()
            })
            .catch((error) => {
              this.status = "failed";
              _.delay(() => {
                this.status = "";
              }, 3000);
            });
        }
      });
    },
    indexSubmit() {
      this.$refs.formValidator.validate().then((valid) => {
        if (valid) {
          this.updateData()
            .then(() => {
              this.$eventHub.$emit("update-success");
              this.$router.push({ path: this.$getViewPath('tables') });
            })
            .catch((error) => {
              this.status = "failed";
              _.delay(() => {
                this.status = "";
              }, 3000);
            });
        }
      });
    }
  },
};
</script>