<template>
  <b-container fluid class="mt--6">
    <card>
      <b-row align-v="center" slot="header">
        <b-col cols="8">
          <h3 class="mb-0">{{ $t('cruds.tables.create.title1') }}</h3>
        </b-col>
      </b-row>

      <validation-observer ref="formValidator">
        <b-form role="form">
          <div class="pl-lg-4">
            <b-row>
              <b-col lg="12">
                <h6 class="heading-small text-muted mb-4">{{ $t('cruds.tables.create.basic_informations') }}</h6>

                <b-row>
                  <b-col lg="4">
                    <base-input :name="$t('cruds.tables.create.area_id')" :label="$t('cruds.tables.create.area_id')"
                      :rules="{ required: true }">
                      <el-select :value="entry.area_id" :placeholder="$t('cruds.tables.create.area_id')" @input="updateAreaId">
                        <el-option v-for="option in lists.areas" :key="option.label"
                          :label="option.label" :value="option.value">
                        </el-option>
                      </el-select>
                    </base-input>
                  </b-col>
                </b-row>

              </b-col>
              <div class="text-center">
                <b-button type="submit" @click.prevent="indexSubmit" variant="primary" class="mt-4">{{ $t('general.save')
                }}</b-button>
                <b-button type="submit" @click.prevent="createSubmit" variant="default" class="mt-4">{{
                  $t('general.save_and_continue') }}</b-button>
              </div>
            </b-row>
          </div>
        </b-form>
      </validation-observer>
    </card>
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters("TableSingle", ["entry", "loading", "lists"]),
  },
  mounted() {
    this.resetState();
    this.fetchCreateData();
  },
  beforeDestroy() {
    this.resetState();
  },
  methods: {
    ...mapActions("TableSingle", [
      "storeData",
      "fetchCreateData",
      "resetState",
      "setAreaId"
    ]),
    updateAreaId(value) {
      this.setAreaId(value);
    },
    createSubmit() {
      this.$refs.formValidator.validate().then((valid) => {
        if (valid) {
          this.storeData()
            .then(() => {
              this.$eventHub.$emit("create-success");
              window.location.reload()
            })
            .catch((error) => {
              this.status = "failed";
              _.delay(() => {
                this.status = "";
              }, 3000);
            });
        }
      });
    },
    indexSubmit() {
      this.$refs.formValidator.validate().then((valid) => {
        if (valid) {
          this.storeData()
            .then(() => {
              this.$eventHub.$emit("create-success");
              this.$router.push({ path: this.$getViewPath('tables') });
            })
            .catch((error) => {
              this.status = "failed";
              _.delay(() => {
                this.status = "";
              }, 3000);
            });
        }
      });
    }
  },
};
</script>