export default {
  data() {
    return {
      query: { sort: "id", order: "desc", offset: 0, limit: 4, s: "", page: 1 },
      search_by_number: "",
      search_by_status: "",
      search_by_area_id: "",
    }
  },
  watch: {
    search_by_number(value) {
      this.updateLinearFilter("id", value);
    },
    search_by_status(value) {
      this.updateLinearFilter("status", value);
    },
    search_by_area_id(value) {
      this.updateLinearFilter("area_id", value);
    }
  },
  methods: {
    eraseFilters() {
      this.clearFilters()
      this.query.page = 1;
      this.resetState();
      this.initFilters(this.query);
    },
    clickPagination(page) {
      this.clearFilters()
      this.query.page = page;
      this.initFilters(this.query);
    },
    initFilters(query) {
      this.setQuery(query);
      this.fetchIndexData();
    },
    updateLinearFilter(column, query_1) {
      this.setLinearFilters({
        column: column,
        query_1: query_1
      });
    },
    clearFilters() {
      this.search_by_number = "";
      this.search_by_status= "";
      this.search_by_area_id = "";
      this.updateLinearFilter("id", '');
      this.updateLinearFilter("status", '');
      this.updateLinearFilter("area_id", '');
    }
  }
}
